document.getElementById("btn24-5").onclick = (event) => {
    event.preventDefault();
    //5.–ó–∞–ø—Ä–æ—Å–∏—Ç—å —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è 10 —á–∏—Å–µ–ª –∏ –ø–æ–¥—Å—á–∏—Ç–∞—Ç—å, —Å–∫–æ–ª—å–∫–æ –æ–Ω –≤–≤–µ–ª –ø–æ–ª–æ–∂–∏—Ç–µ–ª—å–Ω—ã—Ö, –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω—ã—Ö –∏ –Ω—É–ª–µ–π.
    // –ü—Ä–∏ —ç—Ç–æ–º —Ç–∞–∫–∂–µ –ø–æ—Å—á–∏—Ç–∞—Ç—å, —Å–∫–æ–ª—å–∫–æ —á–µ—Ç–Ω—ã—Ö –∏ –Ω–µ—á–µ—Ç–Ω—ã—Ö. –í—ã–≤–µ—Å—Ç–∏ —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫—É –Ω–∞ —ç–∫—Ä–∞–Ω. –£—á—Ç–∏—Ç–µ, —á—Ç–æ –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ –æ–¥–Ω–æ–π –ø–µ—Ä–µ–º–µ–Ω–Ω–æ–π (–Ω–µ 10) –¥–ª—è –≤–≤–æ–¥–∞ —á–∏—Å–µ–ª –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º.
    let zero = 0;
    let minus = 0;
    let plus = 0;
    let even = 0;
    let odd = 0;
    for (let i = 1; i <= 10; i++) {
        let number = +prompt(`insert ten numbers, please, that is number ${i}`);
        if (number == 0) {
            zero += 1;
        } else if (number < 0) {
            minus += 1;
        } else {
            plus += 1;
        }
        if (number % 2 == 0) {
            even += 1;
        } else {
            odd += 1;
        }
    }
    alert(
        `you insert null = ${zero}; minuses = ${minus}; pluses = ${plus}; evens = ${even}; odds = ${odd}`
    );
};
