document.getElementById("btn24-6").onclick = (event) => {
    event.preventDefault();
    //6.–ó–∞—Ü–∏–∫–ª–∏—Ç—å –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä. –ó–∞–ø—Ä–æ—Å–∏—Ç—å —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è 2 —á–∏—Å–ª–∞ –∏ –∑–Ω–∞–∫, —Ä–µ—à–∏—Ç—å –ø—Ä–∏–º–µ—Ä, –≤—ã–≤–µ—Å—Ç–∏ —Ä–µ–∑—É–ª—å—Ç–∞—Ç –∏ —Å–ø—Ä–æ—Å–∏—Ç—å, —Ö–æ—á–µ—Ç –ª–∏ –æ–Ω —Ä–µ—à–∏—Ç—å –µ—â–µ –æ–¥–∏–Ω –ø—Ä–∏–º–µ—Ä.
    // –ò —Ç–∞–∫ –¥–æ —Ç–µ—Ö –ø–æ—Ä, –ø–æ–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –æ—Ç–∫–∞–∂–µ—Ç—Å—è.
    while (true) {
        const numberA = +prompt("For calculate, insert firt number");
        const numberB = +prompt("For calculate, insert firt number");
        const action = prompt("For calculate, insert '*' '/' '+' '-'");
        let result = 0;
        if (action == "*") {
            result = numberA * numberB;
            alert(result);
        } else if (action == "/") {
            result = numberA / numberB;
            alert(result);
        } else if (action == "+") {
            result = numberA + numberB;
            alert(result);
        } else {
            result = numberA - numberB;
            alert(result);
        }
        const conf = confirm("do you want again?");
        if (!conf) {
            break;
        }
    }
};
